.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#hd{
  text-align: center;
  color: red;
  font-size: 40px;
  transform: translate(0px,200px);
  font-size: 20px;
  background-color: violet;
  padding: 20px;
  color: white;
}

.inptg{
  background-color: violet;
  height: 30px; 
  width: 50px;
  padding: 20px;
  border-radius: 30px;
  position: relative;
  top: 100px;
  text-indent: 1px;
  outline: none;
  border: none;
  margin: 20px;
  left: 580px;
  font-weight: bold;
  color: white;
  font-size: 20px;
  text-indent: 4px;
}

.btnbn{
background-color: violet;
  height: 50px;
  width: 200px;
  border: none;
  outline: none;
  border-radius: 30px;
  position: relative;
  font-size: 20px;
  font-weight: bold;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: white;
  
  top: 210px;
  left: 300px;
}

.inptg::placeholder{
  font-size: 20px;
  transform: translate(15px,2px);
  font-weight: bold;
  color: white;
  position: fixed;
  top: 20px;
  left: 20px;
}

body{
  background-color: black;
}